.nav-container {
  width: 98%;
  display: block;
  background: -webkit-linear-gradient(
    to right,
    #fcb045,
    #fd1d1d,
    #833ab4
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#fcb045), color-stop(#fd1d1d), to(#833ab4));
  background: -webkit-linear-gradient(left, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);
  /*   background: black;  */
}

nav {
  height: 60px;
  display: grid;
  grid-template-columns: auto auto;
  align-self: center;
}

nav a {
  text-decoration: none;
  text-transform: uppercase;
  font-family: Arial, sans-serif;
  color: white;
  -webkit-transition: 0.3s all ease-in-out;
  transition: 0.3s all ease-in-out;
}

nav a:hover {
  opacity: 0.7;
}

.logo {
  grid-column: 1;
  text-align: left;
  align-self: center;
}

.logo a {
  padding: 10px 0;
  margin-left: 70px;
  font-size: 25px;
  font-weight: 300;
  letter-spacing: 0.5px;
}

.menu {
  grid-column: 2;
  text-align: right;
  align-self: center;
}

.menu a {
  margin-top: 10px;
  padding: 10px 0;
  margin-right: 30px;
  font-size: 15px;
  font-weight: 200;
  letter-spacing: 0.6px;
}
@media only screen and (max-width: 960px) {
  .nav-container {
    display: none;
  }
}

.bottom {
  overflow: hidden;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: none;
}

@media only screen and (max-width: 960px) {
  .bottom {
    display: block;
  }
}

.App {
  font-family: sans-serif;
  text-align: center;
  margin: 0;
}

.bottom_bar {
  display: none;
}

.AppBar {
  display: block;
  
}
.profileImage {
  margin-bottom: 1%;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  padding: 15px;
}
.banner-text h1 {
  font-size: 3.4rem;
  font-weight: bold;
  color: white;
  margin: 0;
  padding: 0;
}

.banner-text h4 {
  margin: 1%;
  padding: 0;
  font-size: 1.8rem;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 4em;
  padding: 8px;
}
.explore {
  display: none;
}
@media only screen and (max-width: 960px) {
  .bottom_bar {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: block;
  }
  .AppBar {
    display: none;
  }
  .profileImage {
    margin: 10%;
  }

  .banner-text {
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
    padding: 20px;
  }
  .banner-text h1 {
    font-size: 1.8rem;
    font-weight: bold;
    color: white;
    margin: 0;
    padding: 0;
  }

  .banner-text h4 {
    margin: 1%;
    padding: 0;
    font-size: 1rem;
    margin: 10px;
    font-weight: bold;
    color: white;
  }

  .banner-text hr {
    border-top: 3px dotted white;
    width: 50%;
    margin: auto;
  }

  .banner-text p {
    color: white;
    font-size: 10px;
    padding: 1em;
  }

  .social-links {
    display: flex;
    justify-content: space-around;
    width: 70%;
    margin: auto;
  }

  .social-links i {
    color: white;
    font-size: 2em;
    padding: 10px;
  }
  .explore {
    display: block;
  }
}

h5 {
  font-size: 1.5rem;
  margin: 0;
}
h3 {
  color: black;
}
h6 {
  font-size: 1rem;
  margin: 2% 0%;
  letter-spacing: 0.1rem;
}
p {
  letter-spacing: 0.01rem;
  font-size: 0.9rem;
}
.education {
  display: block;
}
.grid {
  height: 85vh;
}
.letter {
  color: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);
}
.rubberBand {
  display: none;
}
@media only screen and (max-width: 960px) {
  h5 {
    font-size: 1.5rem;
    margin: 0;
  }
  h6 {
    font-size: 0.9rem;
    margin: 2% 0%;
    letter-spacing: 0.1rem;
  }
  p {
    letter-spacing: 0.01rem;
    font-size: 0.9rem;
  }
  .bottomBars {
    margin: 0 auto;
    margin-bottom: 1000px;
    margin-top: 50px;
  }
  .grid {
    height: 130vh;
  }
  .rubberBand {
    display: block;
    margin: "0 auto";
  }
  .education {
    margin-bottom: 700px;
  }
}

.pa {
  margin-top: 25px;
  margin-bottom: 20px;
}

@media only screen and (max-width: 960px) {
  .pa {
    margin-top: 50px;
    margin-bottom: 2000px;
  }
}

.close {
  display: block;
}

@media only screen and (max-width: 960px) {
  .close {
    display: none;
  }
  .bottomBar {
    height: 70vh;
  }
}

